import React, { ChangeEvent, useEffect, useRef } from 'react';
import { Input, InputProps } from '@chakra-ui/react';

interface RmgDebouncedInputProps extends InputProps {
    onDebouncedChange?: (value: string) => void;
}

export default function RmgDebouncedInput(props: RmgDebouncedInputProps) {
    const { onDebouncedChange, defaultValue, onChange, ...others } = props;

    const inputElRef = useRef<HTMLInputElement>(null);
    const timeoutRef = useRef<number>();

    useEffect(() => {
        if (inputElRef.current) {
            inputElRef.current.value = defaultValue?.toString() || '';
        }
    }, [defaultValue]);

    const handleChange = ({ target: { value } }: ChangeEvent<HTMLInputElement>) => {
        window.clearTimeout(timeoutRef.current);

        timeoutRef.current = window.setTimeout(() => {
            onDebouncedChange?.(value);
        }, 500);
    };

    return <Input ref={inputElRef} variant="flushed" size="sm" onChange={handleChange} {...others} />;
}
