import React, { ReactNode } from 'react';
import { FormControl, FormLabel } from '@chakra-ui/react';

interface RmgLabelProps {
    label: string;
    children: ReactNode;
}

export default function RmgLabel(props: RmgLabelProps) {
    const { label, children } = props;

    return (
        <FormControl width="unset">
            <FormLabel size="xs" sx={{ fontSize: 12, marginRight: 0, marginBottom: -0.5 }}>
                {label}
            </FormLabel>

            {children}
        </FormControl>
    );
}
