import React from 'react';
import { AccordionButton, AccordionIcon, AccordionItem, AccordionPanel, Box, HStack } from '@chakra-ui/react';
import RmgLabel from '../common/rmg-label';
import RmgDebouncedInput from '../common/rmg-debounced-input';
import { updateStationName, updateStationNum, updateStationSecondaryName } from '../../redux/param/action';
import { useDispatch } from 'react-redux';
import { useAppSelector } from '../../redux';
import { RmgStyle } from '../../constants/constants';

export default function InfoSection() {
    const dispatch = useDispatch();

    const selectedStation = useAppSelector(state => state.app.selectedStation);
    const rmgStyle = useAppSelector(state => state.param.style);
    const { num, name, secondaryName } = useAppSelector(state => state.param.stn_list[selectedStation]);

    const basicFields = [
        [
            {
                type: 'input',
                label: 'Chinese name',
                value: name[0],
                placeholder: '地鐵站',
                onChange: (value: string) => dispatch(updateStationName(selectedStation, [value, name[1]])),
            },
            {
                type: 'input',
                label: 'English name',
                value: name[1],
                placeholder: 'Metro Station',
                onChange: (value: string) => dispatch(updateStationName(selectedStation, [name[0], value])),
            },
        ],
    ];

    const gzmtrFields = [
        [
            {
                type: 'input',
                label: 'Station number',
                value: num,
                placeholder: '01',
                onChange: (value: string) => dispatch(updateStationNum(selectedStation, value)),
            },
            ...basicFields[0],
        ],
        [
            {
                type: 'input',
                label: 'Chinese secondary',
                value: secondaryName ? secondaryName[0] : '',
                placeholder: '1號客運大樓',
                onChange: (value: string) =>
                    dispatch(
                        updateStationSecondaryName(selectedStation, [value, secondaryName ? secondaryName[1] : ''])
                    ),
            },
            {
                type: 'input',
                label: 'English secondary',
                value: secondaryName ? secondaryName[1] : '',
                placeholder: 'Terminal 1',
                onChange: (value: string) =>
                    dispatch(
                        updateStationSecondaryName(selectedStation, [secondaryName ? secondaryName[0] : '', value])
                    ),
            },
        ],
    ];

    const getFields = (style: RmgStyle) => {
        switch (style) {
            case RmgStyle.GZMTR:
                return gzmtrFields;
            default:
                return basicFields;
        }
    };

    return (
        <AccordionItem>
            <h2>
                <AccordionButton size="xs">
                    <Box flex="1" textAlign="left">
                        Basic info
                    </Box>
                    <AccordionIcon />
                </AccordionButton>
            </h2>
            <AccordionPanel>
                {getFields(rmgStyle).map((fieldList, i) => (
                    <HStack key={i}>
                        {fieldList.map((field, j) => (
                            <RmgLabel key={j} label={field.label}>
                                <RmgDebouncedInput
                                    placeholder={field.placeholder}
                                    defaultValue={field.value}
                                    onDebouncedChange={field.onChange}
                                />
                            </RmgLabel>
                        ))}
                    </HStack>
                ))}
            </AccordionPanel>
        </AccordionItem>
    );
}
